import { CountUp } from "countup.js"; // m bo ng c php

export function dropDown() {
  document.addEventListener("DOMContentLoaded", function () {
    // Mng cc phn t cn tm
    const countElements = [
      { id: "stat-number-1", endVal: 100 },
      { id: "stat-number-2", endVal: 200 },
      { id: "stat-number-3", endVal: 300 },
      { id: "number-13", endVal: 13 },
      { id: "number-100", endVal: 100 },
      { id: "number-200", endVal: 200 },
    ];

    // Lp qua cc phn t v khi to CountUp nu phn t tn ti
    countElements.forEach(({ id, endVal }) => {
      const countElement = document.getElementById(id);

      if (countElement) {
        const options = {
          startVal: 0, // Bt u t 0
          endVal: endVal, // Ly gi tr endVal t mng countElements
          duration: 3, // Thi gian chy (2 giy)
          separator: ",", // Ngn cch nghn, triu, ...
          decimals: 0, // S ch s sau du phy
          suffix: "+", // Thm du "+" sau s
        };

        const countUp = new CountUp(id, endVal, options);

        if (!countUp.error) {
          countUp.start();
        } else {
          console.error(countUp.error);
        }
      }
    });

    // Cc chc nng khc
    const menuLinks = document.querySelectorAll(".header-menu a");
    const currentUrl = window.location.href;

    menuLinks.forEach((link) => {
      link.classList.remove("active");

      if (currentUrl.includes(link.href)) {
        link.classList.add("active");
      } else {
        link.classList.remove("active");
      }
    });

    const toggle = document.querySelector(".header-toggle");
    const menu = document.querySelector(".header-menu-mobile");
    const overlay = document.querySelector(".overlay");

    if (toggle && menu && overlay) {
      toggle.addEventListener("click", function () {
        const isMenuVisible = menu.classList.contains("is-show");

        menu.classList.toggle("is-show", !isMenuVisible);
        overlay.classList.toggle("is-show", !isMenuVisible);
      });

      overlay.addEventListener("click", function () {
        menu.classList.remove("is-show");
        overlay.classList.remove("is-show");
      });
    } else {
      console.error("Khng tm thy phn t .header-toggle, .header-menu-mobile hoc .overlay");
    }

    // Search
    const searchBtn = document.querySelector(".header-search");
    const searchBar = document.querySelector(".search-bar-container");

    searchBtn.addEventListener("click", function () {
      searchBar.classList.toggle("active");
    });
  });
}

dropDown();
